<template>
    <div class="new-task">
        <input 
            type="text" 
            class="form-element" 
            placeholder="Nova Tarefa?" 
            v-model="name" 
            @keydown.enter="add">
        <button 
            @click="add" 
            class="form-element"
        >
            +
        </button>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                name: ''
            }
        },
        methods: {
            add() {
                this.$emit('taskAdded', {name: this.name})
                this.name = ''
            }
        }
    }
</script>

<style>
    .new-task {
        margin: 35px;
    }

    .form-element {
        outline: none;
        font-size: 2rem;
        border: 1px solid #fff;
        padding: 5px 10px 8px;
        color: #fff;
    }

    input.form-element {
        width: 500px;
        background-color: #fff2;
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
    }

    button.form-element {
        border: 1px solid #fff;
        background-color: #2196f3;
        color: #fff;
        cursor: pointer;
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
    }

    @media screen and (max-width: 650px) {
        input.form-element {
            width: 300px;
        }
    }

    @media screen and (max-width: 480px) {
        input.form-element {
            width: 150px;
            font-size: 1.6rem;
        }
        button.form-element {
        font-size: 1.6rem;
    }
    }
</style>
